body {
  font-family: "Slabo 27px", sans-serif;
  background: url(./pattern.jpg) repeat;
  margin: 0;
}

a {
  text-decoration: none;
}

a:visited {
  color: inherit;
}

a:hover {
  text-decoration: none;
}
header {
  position: fixed;
  top: 0;
  margin: 0;
  padding: 0;
  background-color: #fff;
  width: 100%;
  box-shadow: 0 0 33px rgba(16, 14, 23, 0.33);
}

header img {
  float: left;
}

#logo {
  display: inline-block;
}





section {
  box-sizing: border-box;
  width: 70%;
  margin-top: 100px;
  float: left;
  /* margin: 100px auto auto auto; */
  padding: 20px;
  background-color: #fff;
  box-shadow: 0 0 33px rgba(16, 14, 23, 0.33);
  border-radius: 5px;
  z-index: 1;
}

section h1 {
  text-align: center;
}

section#bottom {
  margin-top: 10px;
}

#article-list div {
  /* margin-bottom: 10px; */
  margin-top: 5px;
}

#article-list div p {
  margin: 1px;
}

#article-list div p.desc {
  margin-top: 20px;
  /* padding-bottom: 10px; */
  /* border-bottom: #000 solid 1px; */
}

footer {
  text-align: center;
  box-sizing: border-box;
  margin-top: 10px;
  color: #fff;
  background-color: #000;
  margin: 30px;
  height: 30px;
  width: 100%;
}

footer#child {
  z-index: 0;
  max-width: 70%;
  margin: auto;
  margin-top: 10px;
  box-shadow: 0 0 33px rgba(16, 14, 23, 0.33);
  border-radius: 5px;
}

table {
  border-collapse: collapse;
  width: 100%;

}

th {
  background-color: antiquewhite;
}

td,th {
  vertical-align: center;
  text-align: left;
  padding: 20px;
  margin: 20px;
}

td.button {
  text-align: left;
}

/* HEADER */
.login {
  float: right;
  margin-top: 10px;
  margin-right: 50px;
}


/* SIDEBAR */

.sidebar {
  background-color: yellow;
  box-sizing: border-box;
  margin-top: 50px;
  float: left;
  z-index: -1;
  width: 30%;
  height: 100%;
}


nav ul {
  list-style: none;
  margin-top: 10px;
  vertical-align: center;
}

nav ul li {
  width: fit-content;
  display: block;
  margin-right: 0;

}

.table{
  overflow-x:auto !important;
  }

  /* EDITOR */

  .btnTable {
    margin: 10px;
  }

  table thead .caret {
    display: inline-block;
    width: 0;
    height: 0;
    margin-left: 2px;
    vertical-align: middle;
    border-top: 4px dashed;
    border-top: 4px solid \9;
    border-right: 4px solid transparent;
    border-left: 4px solid transparent;
    }
    
    table thead .dropup .caret, table thead .navbar-fixed-bottom .dropdown .caret {
    border-top: 0;
    border-bottom: 4px dashed;
    border-bottom: 4px solid \9;
    content: "";
    }